















































































































import { Vue, Component } from 'vue-property-decorator'
import Types from '../../../../types'
import hljs from 'highlight.js'
import marked from 'marked'
import { Getter } from 'vuex-class'

@Component
export default class addArticle extends Vue {
  @Getter userInfo: any
  form: Types.ArticleData = {
    img: '',
    title: '',
    categoryName: '',
    categoryId: '',
    tagName: [],
    tagId: [],
    abstract: '',
    content: '',
    contentMD: '',
    id: '',
  }
  classData: Array<Types.ArticleClassData> = []
  articleTags: Array<Types.TagsData> = []
  rules: any = {
    title: [{ required: true, message: '请填写文章标题', trigger: 'change' }],
    category: [
      { required: true, message: '请选择文章分类', trigger: 'change' },
    ],
    abstract: [
      { required: true, message: '请填写文章摘要', trigger: 'change' },
    ],
    tags: [{ required: true, message: '请选择文章标签', trigger: 'change' }],
  }
  articleId: any
  isAdd: boolean = true
  theme: string = 'default'

  htmlChange(html: string) {
    this.$nextTick(() => {
      const codes = document.querySelectorAll('.markdown-body pre code')
      codes.forEach((elem) => {
        hljs.highlightBlock(elem)
      })
    })
    this.form.contentMD = html
    this.form.content = marked(html)
  }

  async mounted() {
    const { type, id } = this.$route.query
    this.articleId = id
    this.isAdd = type === 'add'
    // 如果是新增
    if (this.isAdd) {
      const [classData, articleTags] = await Promise.all([
        this.getArticleClass(),
        this.getArticleTags(),
      ]).then((req) => {
        return this.$lo.map(req, (item: Types.InterfaceData) =>
          this.$util.checkResp(item)
        )
      })
      this.classData = classData
      this.articleTags = articleTags
    } else {
      const [classData, articleDetails, articleTags] = await Promise.all([
        this.getArticleClass(),
        this.getArticleDetails(),
        this.getArticleTags(),
      ]).then((req) => {
        return this.$lo.map(req, (item: Types.InterfaceData) =>
          this.$util.checkResp(item)
        )
      })
      this.classData = classData
      this.articleTags = articleTags
      this.form = articleDetails
    }
  }

  // 编辑文章
  async editArticle() {
    console.log(this.form)
    this.form.isUpdate = true
    const { code } = await this.$api.updateArticle(this.form)
    if (code === 0) {
      this.$router.push({ name: 'article@index' })
    }
  }
  // 获取文章标签
  getArticleTags() {
    return this.$api.getArticleTags()
  }

  changeCategory(val: any): void {
    const { name, id } = this.$lo.find(
      this.classData,
      (item: any) => item.id === val
    )
    this.form.categoryName = name
    this.form.categoryId = id
  }

  changeTags(val: Array<string>): void {
    this.form.tagId = val
  }

  getArticleDetails() {
    return this.$api.getArticleDetails({ id: this.articleId })
  }

  // 获取文章分类
  getArticleClass() {
    return this.$api.getArticleClass()
  }

  handleFile(e: any) {
    const $target = e.target
    const file = $target.files[0]
    const type = file.type.split('/')[0]
    if (type != 'image') {
      this.$message.warning('请上传图片')
      return
    }
    const size = Math.round(file.size / 1024 / 1024)
    if (size > 3) {
      this.$message.warning('图片大小不得超过3M')
      return
    }
    const reader = new FileReader()
    reader.readAsDataURL(file)
    const that = this
    reader.onload = function() {
      that.form.img = this.result
    }
  }

  updataImg() {
    const input: any = document.getElementById('file')
    input.click()
  }

  // 新建文章
  async onSubmit(formName: any) {
    if (this.$lo.size(this.form.categoryId) === 0) {
      this.$message.warning('请选择文章分类')
      return
    }
    this.form.author = this.userInfo.name || 'lxx'
    const $refs: any = this.$refs[formName]
    $refs.validate(async (valid: any) => {
      if (valid) {
        const { code } = await this.$api.addArticle(this.form)
        if (code === 0) {
          this.$router.push({ name: 'article@index' })
        }
      } else {
        return false
      }
    })
  }

  // 取消编辑或新增，返回文章列表页
  cancel() {
    this.$router.push({ name: 'article@index' })
  }

  async uploadImage(file: any) {
    return await this.uploadRequest(file)
  }

  uploadRequest(file: any) {
    const result =
      'https://github.com/luosijie/Front-end-Blog/blob/master/img/logo_vmmarkdown_name.png?raw=true'
    return new Promise((resolve, reject) => {
      window.setTimeout(() => {
        resolve(result)
      }, 1000)
    })
  }
}
